<template>
	<view class="container">
		<checkbox-group @change="checkboxChange">
			<block v-if="islogin==0">
				<view class="nologin-box">
					<image src="../../static/images/no-login.png" mode="widthFix" class="nologin"></image>
					<view class="tips">主人，你还没有登录哦~</view>
					<view class="buttons">
						<button type="primary" plain="true" @click="goto(1)" size="mini">逛逛首页</button>
						<button type="primary" @click="goto(2)" size="mini">立即登录</button>
					</view>
				</view>
			</block>
			<block v-else>
				<view class="tools" @click="del">
					删除
				</view>
				<view class="cart-lists" v-if="carts.length>0">
					<view class="item" v-for="(item,index) in carts" :key="index">
						<view class="checkbox">
							<checkbox :value="index" :checked="item.type" style="transform:scale(0.7)" />
						</view>
						<image :src="item.thumb" mode="scaleToFill" class="thumb">
						</image>
						<view class="info">
							<view class="title">
								{{item.title}}
								<text>x{{item.num}}</text>
							</view>
							<view class="num">￥{{item.price}}</view>
							<view class="time">
								<ds-number :total="item.num" :index="index" :cid="item.id" @plus="plus_cart"
									@minus="plus_cart"></ds-number>
							</view>
						</view>
					</view>
				</view>
				<view class="nologin-box" v-else>
					<image src="../../static/images/no-login.png" mode="widthFix" class="nologin"></image>
					<view class="tips">你还没有选购服务哦~</view>
					<view class="buttons">
						<button type="primary" plain="true" @click="goto(1)" size="mini">立即选购</button>
					</view>
				</view>

			</block>
			<view style="height: 80rpx;"></view>
			<view class="jiesuan">
				<view class="check" @click="cancelAll()" v-if="isall">
					<uni-icons type="checkbox-filled" class="icon"></uni-icons>取消
				</view>
				<view class="check" @click="checkAll()" v-else>
					<uni-icons type="checkbox" class="icon"></uni-icons> 全选
				</view>
				<view class="price">
					合计：<text>￥{{total}}</text>
				</view>
				<view class="btn" @click="post">立即下单</view>
			</view>
		</checkbox-group>
	</view>
</template>

<script>
	import dsNumber from "@/components/ds/ds-number.vue";
	export default {
		data() {
			return {
				islogin: 1,
				carts: [],
				checked: false
			}
		},
		onLoad() {

		},
		onShow() {
			this.getCart()
		},
		computed: {
			total() {
				let price = 0
				if (this.carts.length == 0) {
					return price;
				}
				for (let i in this.carts) {
					if (this.carts[i].type == 1) {
						price = price + Number(this.carts[i].price) * Number(this.carts[i].num);
					}
				}
				return price
			},
			ids() {
				let ids = []
				for (let i in this.carts) {
					if (this.carts[i].type == 1) {
						ids.push(this.carts[i].id)
					}
				}
				return ids;
			},
			isall() {
				return this.ids.length == this.carts.length
			}
		},
		components: {
			dsNumber
		},
		methods: {
			async getCart() {
				let res = await this.$u.get('/v2/cart/index', {
					uid: this.vuex_user.id,
				})
				this.carts = res.data.lists
				uni.setTabBarBadge({
					index: 2,
					text: String(res.data.count)
				})

			},
			checkboxChange(e) {
				let ids = e.detail.value
				for (let i in this.carts) {
					this.$set(this.carts[i], 'type', 0)
				}
				ids.forEach((item) => {			
					this.$set(this.carts[item], 'type', 1)
				})
				this.$forceUpdate()

			},
			async del() {
				console.log(this.ids)
				if (this.ids.length == 0) {
					this.$u.toast('请选择服务')
					return
				} 
				let res = await this.$u.post('/v2/cart/del', {
					uid: this.vuex_user.id,
					ids: this.ids
				})
				if (res.code == 200) {
					this.getCart()
				}
			},
			checkAll() {
				for (let i in this.carts) {
					this.$set(this.carts[i], 'type', 1)
				}
				this.$forceUpdate()
			},
			cancelAll() {
				for (let i in this.carts) {
					this.$set(this.carts[i], 'type', 0)
				}
				this.$forceUpdate()
			},
			async plus_cart(e) {
				console.log(e)
				/*添加购物车*/
				let params = {
					uid: this.vuex_user.id,
					id: e.cid,
					num: e.num
				}
				let res = await this.$u.post('/v2/cart/update', params)
				if (res.data.code == 200) {
					this.$set(this.carts[e.index], 'num', e.num)
				} else {
					this.$u.toast(res.data.message)
				}
			},
			goto(index) {
				if (index == 1) {
					uni.switchTab({
						url: '../index/index'
					})
				}
				if (index == 2) {
					uni.navigateTo({
						url: '../login/index'
					})
				}
			},
			async post() {
				if (this.ids.length == 0) {
					this.$u.toast('请选择服务')
					return
				} 
				let res = await this.$u.post('/v2/cart/check', {
					uid: this.vuex_user.id,
					ids: this.ids
				})
				if (res.code == 200) {
					uni.navigateTo({
						url: '../order/pay'
					})
				}
			}
		}
	}
</script>

<style scoped lang="scss">
	.container {
		width: 100vw;
		background-color: #F8F8F8;
		min-height: 100vh;
	}

	.nologin-box {
		display: flex;
		flex-direction: column;
		align-items: center;
		width: 100vw;
	}

	.nologin-box .tips {
		font-size: 32rpx;
		color: #888;
		margin: 40rpx 0;
	}

	.nologin-box .nologin {
		width: 200rpx;
		margin-top: 40rpx;
	}

	.nologin-box .buttons {
		display: flex;
		justify-content: space-around;
		width: 90vw;
	}

	.nologin-box .buttons button {
		width: 40vw;
		border-radius: 40rpx;
	}

	.cart-lists {
		padding: 20rpx;
	}

	.cart-lists .item {
		display: flex;
		background-color: #fff;
		border-radius: 20rpx;
		padding: 20rpx;
		margin-bottom: 20rpx;
		

	}
	.cart-lists .item:last-child{
		border-bottom:none
	}
	.cart-lists .item .thumb {
		width: 180rpx;
		height: 180rpx;
		border-radius: 8rpx;
		margin-right: 20rpx;
	}

	.cart-lists .item .info {
		flex: 1;
		margin-top: 0;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
	}

	.cart-lists .item .info .title {
		font-size: 28rpx;
		display: flex;
		justify-content: space-between;
		margin-bottom: 8rpx;
	}

	.cart-lists .item .info .title text {
		font-size: 24rpx;
		color: #999
	}

	.cart-lists .item .info .num {
		color: #F74437;
	}

	.cart-lists .item .info .time {
		margin-left: auto;
	}

	.checkbox {
		display: flex;
		align-items: center;
		margin-right: 10rpx;
	}

	.tools {
		background-color: #fff;
		padding: 10rpx 20rpx 20rpx 0;
		color: #999999;
		text-align: right;

	}

	.jiesuan {
		background-color: #fff;
		height: 100rpx;
		display: flex;
		justify-content: space-between;
		align-items: center;
		position: fixed;
		bottom: 0;
		width: 100vw;
		font-size: 28rpx;
	}

	.jiesuan .check {
		margin-left: 20rpx;
		font-size: 24rpx;
		display: flex;
		align-items: center;
	}

	.jiesuan .check .icon {
		margin-right: 10rpx;
	}

	.jiesuan .price {
		color: #777
	}

	.jiesuan .price text {
		color: #F74437;
		font-weight: bold;
	}

	.jiesuan .btn {
		width: 160rpx;
		height: 60rpx;
		background-color: #2F80FF;
		color: #fff;
		border-radius: 40rpx;
		text-align: center;
		line-height: 60rpx;
		margin-right: 20rpx;
	}
</style>
